import api from "axios";
import { useNavigate } from "react-router-dom";

// Fonction dconnexion
export default function BoutonDeconnexion(){
    // recuprer le token
    const token = localStorage.getItem('token');
    const route = useNavigate();

    const deconnexion = async() => {
        try {
            const response = await api.post("/deconnexion",
                {},
                { headers: { Authorization: `Bearer ${token}`}}
            );
            // Supprimer du localStorage
            localStorage.removeItem('token');
            localStorage.removeItem('user');

            // Redirectionner vers la page de connexion avec message de succs
            route('/connexion',{
                state:{deconnexionMsg: response.data.message}
            })
        } 
        catch (error) {
            console.error(error.response?.data|| error.message)    
        }
    };

    return (
        <button className="bg-red-950 text-white text-center p-3 rounded-lg hover:bg-red-900 transition cursor-pointer" type="button" onClick={deconnexion}>Se dconnecter</button>
    );
}  